#app {
  font-family: sans-serif;
  align-items: center;
  display: flex;
  height: 200vh;
  background: #c2c2d6;
}

.calculator {
  border: 2px solid #47476b;
  padding: 5px;
  background: black;
  width: 320px;
  position: absolute;
  margin-left: 144px;
}

.formulaScreen {
  min-height: 20px;
  font-size: 20px;
  color: orange;
  text-align: right;
  vertical-align: text-top;
  line-height: 20px;
  overflow-wrap: break-word;
  word-wrap: break-word;
}

.outputScreen {
  font-size: 29px;
  color: white;
  text-align: right;
  line-height: 35px;
}
#result {
  font-size: 25px;
  background: black;
  color: white;
  line-height: 35px;
  text-align: right;
}

button {
  position: relative;
  height: 65px;
  width: 80px;
  color: white;
  outline: 1px solid black;
  background: #4d4d4d;
  font-size: 20px;
  cursor: default;
}

button:hover {
  color: black;
  outline: 0.05em solid grey;
  z-index: 3;
}

.jumbo {
  width: 160px;
}

.author {
  text-align: center;
  margin-top: 535px;
  text-align: center;
  margin-left: 214px;
}

#clear {
  background: rgb(172, 57, 57);
}
#divide {
  background: rgb(102, 102, 102);
}
#multiply {
  background: rgb(102, 102, 102);
}
#subtract {
  background: rgb(102, 102, 102);
}
#add {
  background: rgb(102, 102, 102);
}
#equals {
  background: rgb(0, 68, 102);
  position: absolute;
  height: 130px;
  bottom: 5px;
}
